*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: #666666;
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.00005) 49.88%, rgba(0, 0, 0, 0.5) 100%), url("../img/Pattern.png");
  background-color: #000000;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

svg {
  display: block;
}

button {
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.choices {
  width: 100%;
}

.choices__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}

.choices__title {
  margin-top: 0;
  margin-bottom: 24px;
  font-family: "Exo2.0", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 100;
  line-height: 44px;
  text-align: center;
  text-shadow: 0 1px 1px #000000;
  color: #ffffff;
}

.choices__text {
  margin: 0;
  font-size: 13px;
  line-height: 15px;
  text-align: center;
  color: #ffffff;
}

.choices__text-span {
  color: #1698d9;
  border-bottom: 1px dashed #1698d9;
  cursor: pointer;
}

@media (max-width: 989px) {
  .choices__item:nth-child(3) {
    margin: 0 auto;
  }
}
@media (max-width: 669px) {
  .choices__item:nth-child(3) {
    margin: initial;
  }

  .choices__list {
    justify-content: center;
  }
}
.container {
  max-width: 1150px;
  padding: 0 15px;
  margin: 0 auto;
}

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: calc(100% - 30px);
  max-width: 320px;
  margin-bottom: 14px;
  padding-top: 21px;
  background: #f2f2f2;
  border: 4px solid #1698d9;
  border-radius: 12px;
  clip-path: polygon(11% 0, 100% 0, 100% 100%, 0 100%, 0 8%);
  cursor: pointer;
  transition: all 0.3s ease;
}
.card:hover {
  border-color: #2ea8e6;
}
.card:hover .card__weight-box {
  background-color: #2ea8e6;
}
.card.card--selected {
  border-color: #d91667;
}
.card.card--selected .card__weight-box {
  background-color: #d91667;
}
.card.card--selected-hover {
  border-color: #e52e7a;
}
.card.card--selected-hover .card__weight-box {
  background-color: #e52e7a;
}
.card.card--disabled {
  border-color: #b3b3b3;
}
.card.card--disabled .card__title,
.card.card--disabled .card__subtitle,
.card.card--disabled .card__text {
  color: #b3b3b3;
}
.card.card--disabled .card__img {
  opacity: 0.5;
}
.card.card--disabled .card__weight-box {
  background-color: #b3b3b3;
}
.card.card--disabled + .choices__text {
  color: #ffff66;
}

.card__title {
  margin-top: 0;
  margin-bottom: 5px;
  font-weight: 400;
  font-size: 16px;
  text-align: center;
}
.card__title.card__title--selected-hover {
  color: #e52e7a;
}

.card__subtitle {
  margin-top: 0;
  margin-bottom: 24px;
  padding-left: 54px;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
  color: #000000;
}

.card__subtitle-span {
  display: block;
  font-size: 24px;
  line-height: 28px;
}

.card__text {
  margin-top: 0;
  margin-bottom: auto;
  padding-left: 54px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
}

.card__weight-box {
  position: absolute;
  right: 16px;
  bottom: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  color: #ffffff;
  background: #1698d9;
  border-radius: 50%;
}

.card__weight-number {
  font-size: 42px;
  line-height: 42px;
}

.card__weight {
  font-size: 21px;
  line-height: 22px;
}